    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default {  
		data() {
			return {
				memberInfo:[],
				txwaylist:[],
				current:0,
				//控制滑动效果
				theIndex:null,
				oldIndex:null,
				isStop:false
			}				
		},
        computed: mapState(['hasLogin','userInfo']),
		onLoad() {
			 console.log('页面加载');
			this.loadData();
		},
		onShow(){
			 if (!this.hasLogin) {  
				uni.navigateTo({
					url:'../login',
				})
			}else{
				this.memberInfo = this.userInfo;
			
			}
		},
        methods: {  
			...mapMutations(['setUserInfo']),
            async loadData() {  
				var _this = this;
                if (!this.hasLogin) {  
					uni.navigateTo({
						url:'../login',
					})
                }else{
					this.memberInfo = this.userInfo;

				}
				
				_this.$Request.post('member_fund/txwaylist').then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.txwaylist = res.datas.list;
					}
				}).catch(function (err) {
					console.log(err);
				  });
            },
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			checkTxWay(id){
				for (let i = 0; i < this.txwaylist.length; i++) {
					if (this.txwaylist[i].id === id) {
						this.current = id;
						this.txwaylist[i].is_default=0;
						//break;
					}
					this.txwaylist[i].is_default=0;
				}
				this.$Request.post('member_fund/isdefaut',{id:id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						uni.navigateTo({
							url:"tx", 
							success (res) { 
								let page = getCurrentPages().pop(); 
								if (page == undefined || page == null) { 
								  return ;
								 } 
								 page.onLoad() ;
							}
						})
						
					}
				});
			},
			
			//控制左滑删除效果-begin
			touchStart(index,event){
				console.log('====touchStart====');
				//多点触控不触发
				if(event.touches.length>1){
					this.isStop = true;
					return ;
				}
				this.oldIndex = this.theIndex;
				this.theIndex = null;
				//初始坐标
				this.initXY = [event.touches[0].pageX,event.touches[0].pageY];
			},
			touchMove(index,event){
				console.log('====touchMove====');
				//多点触控不触发
				if(event.touches.length>1){
					this.isStop = true;
					return ;
				}
				let moveX = event.touches[0].pageX - this.initXY[0];
				let moveY = event.touches[0].pageY - this.initXY[1];
				
				if(this.isStop||Math.abs(moveX)<5){
					return ;
				}
				if (Math.abs(moveY) > Math.abs(moveX)){
					// 竖向滑动-不触发左滑效果
					this.isStop = true;
					return;
				}
				
				if(moveX<0){
					this.theIndex = index;
					this.isStop = true;
				}else if(moveX>0){
					if(this.theIndex!=null&&this.oldIndex==this.theIndex){
						this.oldIndex = index;
						this.theIndex = null;
						this.isStop = true;
						setTimeout(()=>{
							this.oldIndex = null;
						},150)
					}
				}
			},
			touchEnd(index,$event){
				console.log('====touchEnd====');
				//结束禁止触发效果
				this.isStop = false;
			},
			//控制左滑删除效果-end
			deleteOrder(id){
				var _this= this;
				console.log('====deleteOrder====');
				_this.$Request.post('member_fund/txway_del',{id:id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						let len = _this.txwaylist.length;
						for(let i=0;i<len;i++){
							if(id==_this.txwaylist[i].id){
								_this.txwaylist.splice(i, 1);
								break;
							}
						}
						/* this.selectedList.splice(this.selectedList.indexOf(orderid), 1);
						this.sum(); */
						_this.oldIndex = null;
						_this.theIndex = null;
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
					}
				});
				
			}
			
			 
        }  
    }  
